:root{
    --text-primary: #ffffff;
    --background-primary: #007bff;
    --background-primary-hover: #0069d9;

    --text-secondary: #ffffff;
    --background-secondary: #6c757d;
    --background-secondary-hover: #5a6268;

    --text-success: #ffffff;
    --background-success: #28a745;
    --background-success-hover: #218838;

    --text-danger: #ffffff;
    --background-danger: #dc3545;
    --background-danger-hover: #c82333;

    --text-warning: #212529;
    --background-warning: #ffc107;
    --background-warning-hover: #e0a800;

    --text-info: #ffffff;
    --background-info: #17a2b8;
    --background-info-hover: #138496;

    --text-light: #212529;
    --background-light: #f8f9fa;
    --background-light-hover: #e2e6ea;

    --text-dark: #ffffff;
    --background-dark: #212529;
    --background-dark-hover: #23272b;
}

*{
  box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,700,900&display=swap');

small {
    font-size: 80%;
    font-weight: 400;
}

label {
    display: inline-block;
    margin-bottom: .5rem;
}

input {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    overflow: visible;
}

select {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  text-transform: none;
  word-wrap: normal;
}

textarea
{
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    overflow: auto;
    resize: vertical;
}

// WC-TEXT-UPPERCASE
.wc-text-uppercase{
    text-transform: uppercase !important;
}

/* Close Button */

.wc-close-button
{
    cursor: pointer;
    background: transparent;
    border: 1px solid transparent;
}

/* Position */

/* Modal */

.wc-modal
{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    z-index: 99999999;
    overflow: hidden;
    overflow-y: auto;

    &.wc-show-modal{
        display: flex;
        overflow-x: hidden;
        overflow-y: auto;
    }
}

.wc-modal-background-fixed
{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.5);
    backdrop-filter: blur(5px);
    -web-kit-backdrop-filter: blur(5px);
}

.wc-modal-dialog{
    position: relative;
    width: auto;
    display: flex;
    flex-direction: column;
    height: 100%;
    max-height: calc(100% - 2rem);
    margin: 1rem auto;
}

.wc-modal-content
{
    display: flex;
    flex-direction: column;
    position: relative;
    width: 500px;
    background: #fff;
    border-radius: 10px;
    font-family: 'Roboto', sans-serif;
    z-index: 99999;
    overflow: hidden;
    margin: auto;
}

.wc-modal-header
{
    padding: 20px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    background: #f2f2f2;
    border-radius: 10px 10px 0px 0px;
}

.wc-modal-header .wc-close-button
{
    font-size: 1.4rem;
    padding: 10px;
}

.wc-modal-body
{
    padding: 20px;
    flex: 1 1 auto;
    overflow-y: auto;
    overflow-x: hidden;
}

.wc-modal-content.wc-modal-sm{
    width: 300px;
}

.wc-modal-content.wc-modal-lg{
    width: 800px;
}

.wc-modal-content.wc-modal-xl{
    width: 1140px;
}

.wc-modal-title
{
    font-weight: 700;
    font-size: 1.5rem;
}

.wc-modal-title h1,
.wc-modal-title h2,
.wc-modal-title h3,
.wc-modal-title h4,
.wc-modal-title h5
{
    margin: 0 !important;
}

.wc-modal-footer
{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
    background: #f2f2f2;
    border-radius: 0px 0px 10px 10px;
}

/* Shadow */

.wc-shadow-lg
{
    box-shadow: 0 20px 30px 5px rgba(0,0,0,0.3);
}

/* Buttons */

.wc-btn {
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .40rem 1rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 2rem;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    margin: 0 .25rem;
}

@media (prefers-reduced-motion:reduce) {
    .wc-btn {
        transition: none;
    }
}

.wc-btn:hover {
    color: #212529;
    text-decoration: none;
}

.wc-btn:focus {
    outline: 0;
    box-shadow: 0 0 0 .2rem rgba(0, 123, 255, .25);
}

.wc-btn:disabled {
    opacity: .65;
}

