/* Form */

/* Form Group */

.wc-form-group
{
    position: relative;
    margin-bottom: 1rem;
}


/* Form control */


/* input */

.wc-form-control
{
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px) !important;
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5 !important;
    color: #495057 !important;
    background-color: #fff !important;
    background-clip: padding-box;
    border: 1px solid #ced4da !important;
    border-radius: 2rem !important;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

@media (prefers-reduced-motion:reduce) {
    .wc-form-control {
        transition: none;
    }
}

.wc-form-control::-ms-expand
{
    background-color: transparent;
    border: 0;
}

.wc-form-control:-moz-focusring
{
    color: transparent;
    text-shadow: 0 0 0 #495057;
}

.wc-form-control:focus
{
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 .2rem rgba(0,123,255,.25);
}

.wc-form-control::-webkit-input-placeholder
{
    color: #6c757d;
    opacity: 1;
}

.wc-form-control::-moz-placeholder
{
    color: #6c757d;
    opacity: 1;
}

.wc-form-control:-ms-input-placeholder
{
    color: #6c757d;
    opacity: 1;
}

.wc-form-control::-ms-input-placeholder
{
    color: #6c757d;
    opacity: 1;
}

.wc-form-control::placeholder
{
    color: #6c757d;
    opacity: 1;
}

.wc-form-control:disabled
{
    background-color: #e9ecef;
    opacity: 1;
}

textarea.wc-form-control
{
    border-radius: 1rem !important;
    height: auto !important;
}

/* input group */

.wc-input-group,
.wc-select-group
{
    display: flex;
    align-items: center;
    position: relative;
}

.wc-input-group > .wc-input-group-prepend + .wc-input-group-field input,
.wc-input-group > .wc-input-group-prepend + input,
.wc-select-group > .wc-select-group-prepend + .wc-select-group-field select
{
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
    border-left: 0px solid transparent !important;
}

/* input group prepend */

.wc-input-group-prepend,
.wc-select-group-prepend
{
    height: calc(1.5em + .75rem + 2px) !important;
    padding: .375rem .75rem !important;
    padding-left: 1rem !important;
    font-size: 1rem;
    line-height: 1.5 !important;
    border: 1px solid #ced4da !important;
    border-top-left-radius: 2rem !important;
    border-bottom-left-radius: 2rem !important;
    color: #495057;
    background: #e6e6e6;
}

.wc-form-text {
    display: block;
    margin-top: .25rem;
}

.wc-text-muted {
    color: #6c757d !important;
}


